import * as yup from "yup";

const formSchema = yup.object({
    amount: yup
        .number()
        .typeError("Coloque um valor a partir de R$ 1000,00")
        .min(1000, "Valor mnimo  R$ 1000")
        .max(100000000, "Valor mximo  100000000")
        .required("O valor de venda  obrigatrio!"),
    installments: yup
        .number()
        .typeError("Quantidade de parcelas  obrigatria!")
        .max(0, "Quantidade mnima  0")
        .max(12, "Quantidade mxima de parcelas  12")
        .required("Quantidade de parcelas  obrigatria!"),
    mdr: yup
        .number()
        .typeError("O percentual  obrigatrio!")
        .required("O percentual  obrigatrio!"),
});

export default formSchema;
